import { observer } from 'mobx-react-lite';
import React from 'react';
import { Link } from 'react-router-dom';
import { Segment, List, Item, Label, Image } from 'semantic-ui-react';
import { Activity } from 'src/app/model/activity';

interface Props {
  activity: Activity;
}

function ActivityDetailSidebar({ activity: { attendees, host } }: Props) {
  return (
    <>
      <Segment textAlign='center' style={{ border: 'none' }} attached='top' secondary inverted color='teal'>
        {attendees?.length} {attendees?.length === 1 ? 'Person' : 'People'} going
      </Segment>
      <Segment attached>
        <List relaxed divided>
          {attendees?.map((attendee) => (
            <Item style={{ position: 'relative' }} key={attendee.username}>
              {host?.username === attendee.username && (
                <Label style={{ position: 'absolute' }} color='orange' ribbon='right'>
                  Host
                </Label>
              )}
              <Image size='tiny' src={attendee.image || '/assets/user.png'} />
              <Item.Content verticalAlign='middle'>
                <Item.Header as='h3'>
                  <Link to={`/profiles/${attendee.username}`}>{attendee.displayName}</Link>
                </Item.Header>
                {attendee.following && <Item.Extra style={{ color: 'orange' }}>Following</Item.Extra>}
              </Item.Content>
            </Item>
          ))}
        </List>
      </Segment>
    </>
  );
}

export default observer(ActivityDetailSidebar);
