import { format } from 'date-fns/esm';
import { observer } from 'mobx-react-lite';
import React from 'react';
import { Link } from 'react-router-dom';
import { Segment, Item, Header, Button, Image, Label } from 'semantic-ui-react';
import { Activity } from 'src/app/model/activity';
import { useStore } from 'src/app/stores/store';

interface Props {
  activity: Activity;
}

const activityImageStyle = {
  filter: 'brightness(30%)',
};

const activityImageTextStyle = {
  position: 'absolute',
  bottom: '5%',
  left: '5%',
  width: '100%',
  height: 'auto',
  color: 'white',
};

function ActivityDetailHeader({ activity }: Props) {
  const {
    activityStore: { updateAttendance, cancelActivityToggle, loading },
  } = useStore();
  return (
    <Segment.Group>
      <Segment basic attached='top' style={{ padding: '0' }}>
        {activity.isCancelled && (
          <Label
            style={{ position: 'absolute', zIndex: 1000, left: -14, top: 20 }}
            ribbon
            color='red'
            content='Cancelled'
          />
        )}
        <Image src={`/assets/categoryImages/${activity.category}.jpg`} fluid style={activityImageStyle} />
        <Segment basic style={activityImageTextStyle}>
          <Item.Group>
            <Item>
              <Item.Content>
                <Header size='huge' content={activity.title} style={{ color: 'white' }} />
                <p>{format(activity.date!, 'dd MMM yyyy')}</p>
                <p>
                  Hosted by{' '}
                  <strong>
                    <Link to={`/profile/${activity.host?.username}`}>{activity.host?.displayName}</Link>
                  </strong>
                </p>
              </Item.Content>
            </Item>
          </Item.Group>
        </Segment>
      </Segment>
      <Segment clearing attached='bottom'>
        {activity.isHost ? (
          <>
            <Button
              color={activity.isCancelled ? 'green' : 'red'}
              basic
              onClick={cancelActivityToggle}
              loading={loading}>
              {activity.isCancelled ? 'Re-activate Activity' : 'Cancel Activity'}
            </Button>
            <Button
              as={Link}
              to={`/manage/${activity.id}`}
              color='orange'
              floated='right'
              disabled={activity.isCancelled}>
              Manage Event
            </Button>
          </>
        ) : (
          <Button
            onClick={updateAttendance}
            loading={loading}
            color={!activity.isGoing ? 'teal' : 'grey'}
            disabled={activity.isCancelled}>
            {activity.isGoing ? 'Cancel attendance' : 'Join Activity'}
          </Button>
        )}
      </Segment>
    </Segment.Group>
  );
}

export default observer(ActivityDetailHeader);
